import React, { useState } from "react";
import "./Main.scss";
import { Slider } from "../Slider/Slider.js";
import { Landing } from "../Landing/Landing.js";

import Leopard from "../images/Leopard14.png";
import Lion from "../images/Lion13.png";
import Tiger from "../images/Tiger13.png";
import Bear from "../images/Bear13.png";
import Rys from "../images/rys13.png";

function Main() {
  const [item, setItem] = useState(0);

  const list = [
    {
      species: "    ",
      age: "       -       ",
      // bio: "Love snacks",
      url: Leopard,
    },
    {
      species: " ",
      age: "       ,         ",
      // bio: "Love shrimps",
      url: Lion,
    },
    {
      species: "   ",
      age: "            ",
      // bio: "Hate Elefants",
      url: Tiger,
    },
    {
      species: "       ",
      age: "        (, ,  ,  ),      ,      ,      ",
      // bio: "Love salmon",
      url: Bear,
    },
    {
      species: "",
      age: "         ,  ,     ",
      // bio: "Love salmon",
      url: Rys,
    },
  ];

  return (
    <div className="Main">
      <Landing animals={list} item={item}></Landing>
      <Slider onItem={(index) => setItem(index)} size={list.length}></Slider>
    </div>
  );
}

export default Main;
